.Nav {
    width: 100%;
    max-width: 800px;
    z-index: 100;
    position: absolute;
    right: 0;
    bottom: 3px;
}

.quickScan{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: auto;
    border: 1px solid rgba(0,0,0,.3);
}

.quickScanBtn{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: auto;
    background: rgba(255,255,255,1);
    border: 1px solid rgba(50,50,50,.4);
}

.quickScanBtn i {
    color: rgba(0,0,0,.5);
}

.dark-quickScan{
    background: rgba(250,250,250,.4);
    border: .5px solid rgba(250,250,250,.8);
}

.dark-quickScanBtn{
    background: rgba(250,250,250,.4);
    border: .5px solid rgba(250,250,250,.8);
}

.logout, .goBack{
    position: absolute;
    right: 4%;
    bottom: 17%;
    background-color: transparent;
    border: none;
    outline: none;
    color: rgba(150,150,150,1);
    font-size: .7rem;
    opacity: 0.7;
}

.logout:active, .goBack:active{
    opacity: 1;
}

.logout:focus, .goBack:focus{
    border: none;
    outline: none;
    box-shadow: none;
}

.goBack{
    left: 4%;
    right: initial;
}